.søknadSendt {
  max-width: 704px;
  padding: 2rem;
  display: flex;
  flex-direction: column;
  max-width: 726px;
  padding: 0 1rem;
  margin: 0 auto;
  margin-bottom: 2rem;
}
@media (max-width: 68px) {
  .søknadSendt {
    max-width: 100%;
  }
}
@media (max-width: 576px) {
  .søknadSendt {
    padding: 1rem;
  }
}
.søknadSendt__foreldrepengerPanel {
  background-color: #e0f5fb;
}
.søknadSendt__tittel {
  overflow: hidden;
  text-align: center;
}
.søknadSendt__tittel:after {
  content: '';
  display: inline-block;
  height: 0.75rem;
  vertical-align: bottom;
  width: 100%;
  margin-right: -100%;
  padding-right: 0.5rem;
  margin-left: 1rem;
  border-top: 1px solid #c6c2bf;
}
.søknadSendt__tittel:before {
  content: '';
  display: inline-block;
  height: 0.75rem;
  vertical-align: bottom;
  width: 100%;
  margin-left: -100%;
  padding-left: 0.5rem;
  margin-right: 1rem;
  border-top: 1px solid #c6c2bf;
}
